.banner {
    height: calc(100vh - 64.26px);
    min-height: 200px;
    width: 100%;
    background: url("../../images/Banner.jpg");
    background-attachment: fixed;
    background-size: cover;
    background-position: 50% 50%;
}

.home {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.home h1 {
    min-height: 200px;
}

.home button {
    animation: brilhar 1s infinite;
}

@keyframes brilhar {
    0% {
        box-shadow: inset 0 0 3px var(--amarelo), 0 0 3px var(--amarelo);
    }
    25% {
        box-shadow: inset 0 0 6px var(--amarelo), 0 0 6px var(--amarelo);
    }
    50% {
        box-shadow: inset 0 0 8px var(--amarelo), 0 0 8px var(--amarelo);
    }
    75% {
        box-shadow: inset 0 0 11px var(--amarelo), 0 0 11px var(--amarelo);
    }
    100% {
        box-shadow: none;
    }
}