header {
    background: var(--azul);
    border-bottom: var(--borda) var(--amarelo);
}

header div.menu {
    height: auto;
    padding: 7.5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header nav {
    position: absolute;
    top: 64.26px;
    transform: translateX(100vw);
    transition: transform .25s linear;
}

.menu-toggle div {
    height: 3px;
    width: 20px;
    background: var(--branco);
    margin: 2px 0;
    border-radius: 25px;
}

header .menu.on nav {
    display: block;
    display: flex;
    flex-direction: column;
    transform: translateX(0);
    transition: transform .25s linear;
    height: auto;
    width: 100vw;
    background: var(--preto);
}

header .menu.on nav li {
    padding: 10px 25px;
    text-align: center
}

header .menu.on .two {
    opacity: 0;
}

header .menu.on .one {
    transform: rotate(45deg) translate(3px, 3px)
}

header .menu.on .three {
    transform: rotate(-45deg) translate(4px, -4px)
}

header .menu .one, .two, .three {
    transition-duration: 0.3s;
}

header nav div {
    border-top: var(--borda) #444;
    width: 65%;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

header .menu.on nav div li {
    padding: 10px;
}

header nav div i {
    font-size: 30px;
}

header nav div i.fa-whatsapp {
    color: #4EBF61;
}

header nav div i.fa-facebook {
    color: #3F90FB;
}

header nav div i.fa-instagram {
    background: -webkit-linear-gradient(#485FAB, #EF2D76, #FFE292);
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

header nav div i.fa-map-marker-alt {
    color: #D93B2D;
}

@media screen and (min-width: 770px) {
    .menu-toggle {
        display: none;
    }

    header nav {
        display: block;
        position: unset;
    }

    header nav ul {
        display: flex;
    }

    header nav ul li {
        padding: 0 40px;
    }

    header nav div {
        display: none;
    }

    header nav ul li a {
        transition-duration: 0.2s;
    }

    header nav ul li a:hover {
        color: var(--amarelo);
        text-shadow: 0 0 1.5px var(--amarelo);
        transition-duration: 0.2s;
    }
}