/*Barlow font*/
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@400;600;800&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: "Barlow", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: var(--branco);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

:root {
    --azul: #191E74;
    --amarelo:#FEC309;
    --preto: #222222;
    --branco: #EFEFEF;
    --borda: solid 3px;
}

.container-lg {
    width: 92.5%;
    max-width: 1000px;
    height: auto;
    margin: auto;
}

.container-wd {
    width: 89%;
    max-width: 850px;
    height: auto;
    margin: auto;
}

.logo {
    width: 120px;
}

.titulo {
    font-weight: 800;
    font-size: 2.5em;
    text-align: center;
}

.texto-bold {
    font-weight: 600;
    text-align: center;
}

.texto-light {
    font-weight: 400;
    text-align: center;
}

.texto-branco {
    color: var(--branco);
}

.texto-preto {
    color: var(--preto);
}

.texto-amarelo {
    color: var(--amarelo);
}

button {
    background: transparent;
    padding: 5px 20px;
    border: var(--borda) var(--amarelo);
    border-radius: 50px;
    display: flex;
    outline: none;
}

button i {
    font-size: 1.25em;
    padding-right: 4px;
}