@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;600;800&display=swap);
/*Barlow font*/

* {
    margin: 0;
    padding: 0;
    font-family: "Barlow", 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: #EFEFEF;
    background: var(--branco);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

:root {
    --azul: #191E74;
    --amarelo:#FEC309;
    --preto: #222222;
    --branco: #EFEFEF;
    --borda: solid 3px;
}

.container-lg {
    width: 92.5%;
    max-width: 1000px;
    height: auto;
    margin: auto;
}

.container-wd {
    width: 89%;
    max-width: 850px;
    height: auto;
    margin: auto;
}

.logo {
    width: 120px;
}

.titulo {
    font-weight: 800;
    font-size: 2.5em;
    text-align: center;
}

.texto-bold {
    font-weight: 600;
    text-align: center;
}

.texto-light {
    font-weight: 400;
    text-align: center;
}

.texto-branco {
    color: #EFEFEF;
    color: var(--branco);
}

.texto-preto {
    color: #222222;
    color: var(--preto);
}

.texto-amarelo {
    color: #FEC309;
    color: var(--amarelo);
}

button {
    background: transparent;
    padding: 5px 20px;
    border: solid 3px #FEC309;
    border: var(--borda) var(--amarelo);
    border-radius: 50px;
    display: flex;
    outline: none;
}

button i {
    font-size: 1.25em;
    padding-right: 4px;
}
header {
    background: var(--azul);
    border-bottom: var(--borda) var(--amarelo);
}

header div.menu {
    height: auto;
    padding: 7.5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header nav {
    position: absolute;
    top: 64.26px;
    transform: translateX(100vw);
    transition: transform .25s linear;
}

.menu-toggle div {
    height: 3px;
    width: 20px;
    background: var(--branco);
    margin: 2px 0;
    border-radius: 25px;
}

header .menu.on nav {
    display: block;
    display: flex;
    flex-direction: column;
    transform: translateX(0);
    transition: transform .25s linear;
    height: auto;
    width: 100vw;
    background: var(--preto);
}

header .menu.on nav li {
    padding: 10px 25px;
    text-align: center
}

header .menu.on .two {
    opacity: 0;
}

header .menu.on .one {
    transform: rotate(45deg) translate(3px, 3px)
}

header .menu.on .three {
    transform: rotate(-45deg) translate(4px, -4px)
}

header .menu .one, .two, .three {
    transition-duration: 0.3s;
}

header nav div {
    border-top: var(--borda) #444;
    width: 65%;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

header .menu.on nav div li {
    padding: 10px;
}

header nav div i {
    font-size: 30px;
}

header nav div i.fa-whatsapp {
    color: #4EBF61;
}

header nav div i.fa-facebook {
    color: #3F90FB;
}

header nav div i.fa-instagram {
    background: -webkit-linear-gradient(#485FAB, #EF2D76, #FFE292);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

header nav div i.fa-map-marker-alt {
    color: #D93B2D;
}

@media screen and (min-width: 770px) {
    .menu-toggle {
        display: none;
    }

    header nav {
        display: block;
        position: unset;
    }

    header nav ul {
        display: flex;
    }

    header nav ul li {
        padding: 0 40px;
    }

    header nav div {
        display: none;
    }

    header nav ul li a {
        transition-duration: 0.2s;
    }

    header nav ul li a:hover {
        color: var(--amarelo);
        text-shadow: 0 0 1.5px var(--amarelo);
        transition-duration: 0.2s;
    }
}
.banner {
    height: calc(100vh - 64.26px);
    min-height: 200px;
    width: 100%;
    background: url(/santos-baterias/static/media/Banner.32b31046.jpg);
    background-attachment: fixed;
    background-size: cover;
    background-position: 50% 50%;
}

.home {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.home h1 {
    min-height: 200px;
}

.home button {
    animation: brilhar 1s infinite;
}

@keyframes brilhar {
    0% {
        box-shadow: inset 0 0 3px var(--amarelo), 0 0 3px var(--amarelo);
    }
    25% {
        box-shadow: inset 0 0 6px var(--amarelo), 0 0 6px var(--amarelo);
    }
    50% {
        box-shadow: inset 0 0 8px var(--amarelo), 0 0 8px var(--amarelo);
    }
    75% {
        box-shadow: inset 0 0 11px var(--amarelo), 0 0 11px var(--amarelo);
    }
    100% {
        box-shadow: none;
    }
}
#quemsomos {
    margin: 80px auto;
    height: auto;
}

#quemsomos li {
    display: flex;
    align-items: center;
    padding: 10px 0;
    text-align: left;
}

#quemsomos ul {
    margin: 30px auto;
    width: auto;
    min-width: auto;
    max-width: 270px;
}

.circulo {
    height: 10px;
    width: 10px;
    background-color: var(--azul);
    border-radius: 100%;
    margin-right: 10px;
}
#visitenos p {
    margin: 30px 0;
}

#visitenos .endereco {
    text-align: center;
}

#visitenos iframe {
    width: 100%;
    border: var(--borda) var(--amarelo);
    border-radius: 10px;
}
footer {
    height: auto;
    width: 100%;
    background: var(--azul);
    margin-top: 80px;
    border-top: var(--borda) var(--amarelo);
}

footer .logo {
    margin: 0 auto;
    display: block;
    padding: 15px 0;
}

footer p {
    padding-bottom: 15px;
}

footer .creditos {
    background: var(--preto);
}


